<template>
  <div>
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>

    <!-- 离你最近 起始 -->
    <div class="fiveCinema" v-if="!value">
        <p>离你最近</p>
        <ul>
            <li v-for="(data,index) in getFiveCinema" :key="index">{{data.name}}</li>
        </ul>
    </div>
    <!-- 离你最近 结尾 -->
    
    <!-- cinema start -->
    <div v-if="value" class="cinemalist">
        <ul>
            <li v-for="(val,index) in filterCinema" :key="index">
                <p>{{val.name}}</p>
                <span>{{val.address}}</span>
            </li>
        </ul>
    </div>
    <!-- cinema end -->
  </div>
</template>

<script>
import { mapGetters,mapState } from "vuex";
import Vue from "vue";
import { Search, Toast } from "vant";

Vue.use(Search);
export default {
  data() {
    return {
      value: ""
    };
  },
  methods: {
    onSearch(val) {
      Toast(val);
    },
    onCancel() {
      this.$router.back()
    }
  },
  computed: {
    ...mapGetters("cinema", ["getFiveCinema"]),
    ...mapState("cinema", ["datalist"]),
    filterCinema(){
        return this.datalist.filter(item=>item.name.toUpperCase().includes(this.value.toUpperCase()) || item.address.toUpperCase().includes(this.value.toUpperCase()))
    }
  },
  created() {
    console.log(this.getFiveCinema);
  }
};
</script>
<style lang="scss" scoped>
.cinemalist{
    position: fixed;
    top: 50px;
    width: 100%;
    padding: 0 15px;
    ul{
        li{
            margin-bottom: 10px;
            p{
                font-size: 16px;
                color: black;
            }
            span{
                display: block;
                color: orange;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                width: 2.7rem;
            }
        }
    }
}
.fiveCinema{
    position: fixed;
    top: 50px;
    width: 100%;
    padding: 0 15px;
    p{
        line-height: 22px;
    }
    ul{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-flow: wrap;
        li{
            background: rgb(250, 240, 240);
            padding: 2px;
            margin: 0 2px 5px 2px;
        }
    }
}
form{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}
</style>